import React from 'react';

const Student = ({ students, onRemove }) => {
  const list_style = {
    listStyle: 'none',
    display: 'flex',
    // width: '800px',
    // gap: '20px',
    lineHeight: '40px',
    justifyContent: 'space-between',
    textAlign: 'left'
  }

  return (
    <>
      <ul style={list_style}>
        <li> : {students.name}</li>
        <li> : {students.age}</li>
        <li> : {students.email}</li>
        <li><button onClick={() => onRemove((students.id))}> </button></li>
      </ul>
    </>
  );
};

export default Student;