import React, {useState} from 'react';

function Form2(props) {
  //
  let header = '';

  //    
  const [name, setName] = useState('');
  const [pw_name, setPw] = useState('');

  //   nameChange 
  const nameChange=(e)=>{//   
    setName(e.target.value);// 
  }
  //pw    pwChange
  const pwChange=(e)=>{//   
    setPw(e.target.value); // 
  }

  //     ,     .
  if(name){
    header=<p>   {name},  {pw_name}.</p>
  }else{
    header="";
  }

  //onDataReset  name, pw_name 
  const onDataReset=(e)=>{
    setName('');
    setPw('');
  }

  return (
    <>
      <h4 className="title4">2. if  </h4>

      {/* name, pw_name  */}
      {header}

      <form>
        <p>
          <label for="id_name">ID : </label>
          <input type="text" id="id_name" name="id_name" value={name} onChange={nameChange} />
        </p>

        <p>
          <label for="pw_name">PW : </label>
          <input type="text" id="pw_name" name="pw_name" value={pw_name} onChange={pwChange} />
        </p>
      </form>
      <p>
        <button tpye="button" onClick={onDataReset}>  </button>
      </p>
    </>
  );
}

export default Form2;