import React from 'react';

//    => name='000'
function Child({ name = '000', age = '00', hobby = '000' }) {
  return (
    <>
      <h3> (Child)</h3>
      <ul>
        <li> : {name}</li>
        <li> : {age}</li>
        <li> : {hobby}</li>
      </ul>
    </>
  );
}

export default Child;