import React, {useState} from 'react';

function Form4(props) {

  //1.  
  // const [name, setName] = useState('');
  // const [id_txt, setId] = useState('');

  //2.    
  const [formData, setFormData] = useState({
    name:'',
    id_txt:'',
  });

  //3.      
  const nameChange=(e)=>{
  //   setName(e.target.value);
  // }
  // const id_txtChange=(e)=>{
  //   setId(e.target.value);
  // }

    const {name, value} = e.target;
    setFormData((prev)=>({
      ...prev,  //es6  (...) -    
      [name]:value
    }));
  }

  //   ,   
  const onDelete =()=>{
    // setName(''); //  
    // setId(''); //  

    setFormData({
      name:'',
      id_txt:''
    })
  };

  return (
    <>
      <h4>4.  input  </h4>
      <ul>
        <li>e.target.value : (value) </li>
        <li>e.target.name : name  </li>
        <li>state   </li>
        <li>onChange    </li>
        <li>useState         .</li>
      </ul>

      <form>
        <p>() : </p>
        <input type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={nameChange}
        />

        <p>() : </p>
        <input type="text" 
          id="id"
          name="id_txt"
          value={formData.id_txt}
          onChange={nameChange}
        />
      </form>

      <p>  '{formData.name}',  '{formData.id_txt}'.</p>

      <input type="button" value="" onClick={onDelete} />
    </>
  );
}

export default Form4;