import React, { useEffect, useState } from 'react';

const Noodle = () => {
  const [noodle, setNoodle] = useState([]);
  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/noodle.json`)
      .then(res => res.json())
      .then(data => setNoodle(data))
      .catch(err => console.error('   : ', err))
  }, [])

  return (
    <>
      <h4 className="title4">noodle  </h4>
      <ul className="list01">
        {noodle.map(noodle => (
          <li key={noodle.num}>
            num : {noodle.num}
             : {noodle.name}
             : {noodle.company}
             : {noodle.kind}
             : {noodle.price}
             : {noodle.e_date}
          </li>
        ))}
      </ul>
    </>
  );
};

export default Noodle;