import React, { useEffect, useState } from 'react';

const UseEffect1 = () => {
  // ,  =  
  const [count, setCount] = useState(0);

  useEffect(() => {
    //     
    console.log(`count  ->   : ${count}`)
  }, [count]) // count   

  return (
    <div>
      <p> count: {count}</p>
      <button onClick={() => setCount(count + 1)}></button>
    </div>
  );
};

export default UseEffect1;