import React, { useEffect, useState } from 'react';

const Fruits = () => {
  const [fruits, setFruits] = useState([]);
  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/fruit.json`)
      .then(res => res.json())
      .then(data => setFruits(data))
      .catch(err => console.error('  : ', err))
  }, [])

  return (
    <>
      <h4 className="title4">fruits  </h4>
      <ul className="list01">
        {fruits.slice(0, 5).map(fruit => (
          <li key={fruit.num}>
            num : {fruit.num}
             : {fruit.name}
             : {fruit.price}
             : {fruit.color}
             : {fruit.country}
          </li>
        ))}
      </ul>
    </>
  );
};

export default Fruits;