import React, { useRef, useState } from 'react';
import StudentInput from './StudentInput'
import Student from './Student'

const StudentList = () => {
  //     = 
  const [students, setStudents] = useState([
    {
      id: 'st001',
      name: '',
      age: 28,
      email: 'rm@gmail.com'
    },
    {
      id: 'st002',
      name: '',
      age: 30,
      email: 'kim111@gmail.com'
    },
    {
      id: 'st003',
      name: '',
      age: 21,
      email: 'ohsujin@gmail.com'
    },
    {
      id: 'st004',
      name: '',
      age: 33,
      email: 'choigil@gmail.com'
    },
    {
      id: 'st005',
      name: '',
      age: 28,
      email: 'kim1924@gmail.com'
    }
  ])

  //   5    6  
  const nextId = useRef(6);

  //    state 
  const [inputs, setInputs] = useState({
    name: '',
    age: '',
    email: ''
  })

  //    state   
  const { name, age, email } = inputs;

  // 1.    
  const onDataChange = (e) => {
    // name value   input   
    const { name, value } = e.target;

    // state 
    setInputs({
      ...inputs, //     
      [name]: value
    })
  }

  // 2.   
  const onCreate = () => {
    //   
    if (!name.trim() || !age || !email.trim()) {
      alert(', ,    ');
      return;
    }

    //   
    const student = {
      id: 'st00' + nextId.current,
      name,
      age: parseInt(age, 10),
      email
    }

    //   -      
    setStudents([...students, student]);

    //   
    setInputs({
      name: '',
      age: '',
      email: ''
    })

    // id 1  
    // nextId.current = nextId.current + 1
    nextId.current += 1;
  }

  // 3.       student.id  
  //      
  const onRemove = (id) => {
    //       
    setStudents(students.filter((student) => student.id !== id));
  }

  const list_box = {
    width: '800px'
  }

  return (
    <>
      <div style={list_box}>
        {/*    */}
        <StudentInput name={name} age={age} email={email} onDataChange={onDataChange} onCreate={onCreate} />

        {/* 1.     */}
        {/* <ul>
          <li> : {students[0].name}</li>
          <li> : {students[0].age}</li>
          <li> : {students[0].email}</li>
        </ul> */}

        {/* 2.   props     */}
        {/* <ul>
          <li><Student students={students[0]} /></li>
          <li><Student students={students[1]} /></li>
          <li><Student students={students[2]} /></li>
          <li><Student students={students[3]} /></li>
          <li><Student students={students[4]} /></li>
        </ul> */}

        {/*     */}
        {/* 
            
          - map         
          - map(( ,   , ),  this  )
          - array.map(functnion(currentValue, index, array) {
             
          }, thisArg)
          -      key  
         */}
        {students.map((student) => (
          <Student key={student.id} students={student} onRemove={onRemove} />
        ))}
      </div>
    </>
  );
};

export default StudentList;