import React from 'react';
import dummy from './data/orders.json';

const Orders = () => {
  return (
    <>
      <h4 className="title4">orders  </h4>
      <ul className="list01">
        {dummy.slice(0, 5).map((order, i) => (
          <li key={i}>
             : {order.o_day}
             : {order.c_name}
             : {order.g_code}
             : {order.o_num}
          </li>
        ))}
      </ul>
    </>
  );
};

export default Orders;