import React, { Component } from 'react';

class Count1 extends Component {
  //  
  state = { n: 1 };

  //  
  onPlus = () => {
    this.setState({
      n: this.state.n + 1
    })
  }

  onMinus = () => {
    if (this.state.n === 1) {
      this.state.n = 1;
    } else {
      this.setState({
        n: this.state.n - 1
      })
    }
  }

  render() {
    return (
      <>
        <p>+,-   ,   state </p>
        <div>
          <input type="button" value="-" className='btn' onClick={this.onMinus} />
           : <span className='num'>{this.state.n}</span>
          <input type="button" value="+" className='btn' onClick={this.onPlus} />
        </div>
      </>
    );
  }
}

export default Count1;