import React from 'react';
import dummy from './data/customer.json'

const Customer = () => {
  return (
    <>
      <h4 className="title4">customer </h4>
      <ul className="list01">
        {dummy.map(customer => (
          <li key={customer.c_name}>
             : {customer.c_name}
             : {customer.c_address}
             : {customer.c_tel}
          </li>
        ))}
      </ul>
    </>
  );
};

export default Customer;