import React, { useEffect, useState } from 'react';

const Form6 = () => {
  // 
  const [description, setDescription] = useState(' textarea  ');
  const [menu, setMenu] = useState(''); //  

  // textarea    
  const handleChange = (e) => {
    setDescription(e.target.value)
  }

  //  select, option   
  const menuChange = (e) => {
    setMenu(e.target.value)
    console.log(menu) //      
    console.log(e.target.value) //    
  }

  //   
  const txt_color_red = {
    color: 'red'
  }

  return (
    <>
      <h4 className="title4">6. textarea, select </h4>
      <p>html textarea     React jsx textarea value=""  </p>
      <pre>
        * html  : <textarea name="" id=""></textarea>
        <br />
        * jsx  : <textarea name="" id="" value={''}></textarea>
      </pre>
      <textarea
        name="content" id="content" cols="30" rows="10"
        value={description}
        onChange={handleChange}
      />
      <p>textarea    : <span style={txt_color_red}>{description}</span></p >

      <p>select    </p>
      <pre>
        * html 
        <select>
          <option value=""></option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
        </select>
        <br />
        *jsx 
        <select value={``}>
          <option value=""></option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
        </select>
        * select  value   
      </pre>

      <h4>  </h4>
      <select value={menu} onChange={menuChange}>
        <option value=""> </option>
        <option value=""></option>
        <option value=" "> </option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <p>     <span style={txt_color_red}>'{menu}'</span></p>
      <ul className='list01'>
        <li>console.log(menu) - useState      </li>
        <li> 1: console.log(e.target.value)</li>
        <li>
           2: useEffect  <br />
          <pre>
            {`1 : import {useEffect} from 'react';

              : 
            useEffect(() => {
              console.log('  :' + menu);
            }, [menu]) //      
            
            useEffect()         
            1.    
            2.  /   
            3. state()  
            `}
          </pre>
        </li>
      </ul>
    </>
  );
};

export default Form6;