import React from 'react';
import dummy from './data/goods.json';

const Goods = () => {
  return (
    <>
      <h4 className="title4">goods  </h4>
      <ul className='list01'>
        {dummy.slice(0, 5).map(goods => (
          <li key={goods.g_code}>
             : {goods.g_code}
             : {goods.g_name}
             : {goods.g_cost}
          </li>
        ))}
      </ul>
    </>
  );
};

export default Goods;