import React, { useState } from 'react';

const Loginform = () => {
  //   ==  
  const [inputs, setInputs] = useState({
    username: '',
    userid: ''
  })

  //    
  const { username, userid } = inputs

  //  (      )
  const onDataChange = (e) => {
    const { value, name } = e.target; //  input name  

    setInputs({
      ...inputs, //  input 
      [name]: value // name   value 
    });
  }

  //  
  const onDataReset = (e) => {
    setInputs({
      username: '',
      userid: ''
    });
  }

  return (
    <>
      <h3 className="title3">useState   '', ''     ''   </h3>

      <ul className="list01">
        <li> input    name, value</li>
        <li>e.target.value //    value</li>
        <li>e.target.name //    name</li>
      </ul>

      <fieldset>
        <legend></legend>
        <p>
          <label htmlFor="user_name"> : </label>
          <input type="text" name="username" id='user_name' placeholder=' ' value={username} onChange={onDataChange} />
        </p>
        <p>
          <label htmlFor="user_id"> : </label>
          <input type="text" name="userid" id='user_id' placeholder=' ' value={userid} onChange={onDataChange} />
        </p>

        <input type="button" value="  " onClick={onDataReset} />

        <p> : {username} : ({userid})</p>
      </fieldset>

      <dl>
        <dt> </dt>
        <dd> 1  useState()     </dd>
        <dd>     2  2    (username, userid) =  useState()   </dd>
        <dd>      </dd>
        <dd>input.username;</dd>
        <dd>ipnut.userid;</dd>
        <dt>      </dt>
        <dd>inputs.username;</dd>
        <dd>inputs.userid;</dd>
        <dt>    </dt>
        <dd>const username, userid = inputs;</dd>
        <dd>... :  inputs     </dd>
        <dd>      </dd>
        <dd>[name]: value ,   key[]        key </dd>
      </dl>
    </>
  );
};

export default Loginform;