import React, { useState, useEffect } from 'react';
import dummy from './data/book_store.json';

const BookStore = () => {
  const [books, setBooks] = useState([]); //  

  //    
  useEffect(() => {
    // fetch('./data/book_store.json') // 1
    fetch(`${process.env.PUBLIC_URL}/data/book_store.json`) // 2
      .then(response => response.json())
      .then(json => setBooks(json))
      .catch(err => console.error('   : ', err))
  }, [])

  return (
    <>
      <h3 className='title3'>book_store json </h3>
      <p>1.    import  ()</p>
      <p>map slicd   </p>
      <ul className='list01'>
        <li>slice(start, end)</li>
        <li>start :   </li>
        <li>end :    </li>
        <li>slice(0,5) 0  4   5   </li>
      </ul>
      <ul className='list01'>
        {dummy.slice(0, 5).map(book => (
          <li key={book.num}>
             num: {book.num}
             : {book.name}
             1: {book.area1}
             2 : {book.area2}
             3 : {book.area3}
               : {book.BOOK_CNT}
              : {book.owner_nm}
              : {book.tel_num}
          </li>
        ))}
      </ul>

      <p>2. fetch     </p>
      <ul className='list01'>
        {books.slice(0, 4).map(book => (
          <li key={book.num}>
             num: {book.num}
             : {book.name}
             1: {book.area1}
             2 : {book.area2}
             3 : {book.area3}
               : {book.BOOK_CNT}
              : {book.owner_nm}
              : {book.tel_num}
          </li>
        ))}
      </ul>
    </>
  );
};

export default BookStore;