import React from 'react';
import Main from './Main'

const Array1 = (props) => {
  // 1 
  const names = ['', '', '', ''];

  // 2 
  const students = [
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
  ]

  // map       
  const studentsList = students.map(student => <Main key={student.id} id={student.id} name={student.name} />);

  return (
    <>
      <h4 className="title4">       , props</h4>
      {studentsList}

      <h3 className="title3">     </h3>
      <p> ,    </p>
      <ul className='list01'>
        <li>for</li>
        <li>while</li>
        <li>do while</li>
      </ul>

      <ol className='list01'>
        <li>for     : for(;;)  ;</li>
        <li>map  -             . map   '       .          'map'.          '  '   </li>
        <li>   -      </li>
      </ol>
      <p> : array.reduce(claaback(accumulator, currentValue, index, array), initialValue)</p>
    </>
  );
};

export default Array1;