import React from 'react';

const StudentInput = ({ name, age, email, onDataChange, onCreate }) => {
  const border_style = {
    border: '3px solid #333',
    padding: '10px'
  }
  const button_style = {
    border: 'none',
    background: '#333',
    color: '#fff',
    width: '120px',
    height: '34px'
  }
  const input_style = {
    height: '30px',
    margin: '3px'
  }

  return (
    <>
      <div style={border_style}>
        <input type="text" name="name" placeholder=' ' value={name} onChange={onDataChange} style={input_style} />

        <input type="text" name="age" placeholder=' ' value={age} onChange={onDataChange} style={input_style} />

        <input type="email" name="email" placeholder='  id@domain.com' value={email} onChange={onDataChange} style={input_style} />

        <input type="button" value=" " style={button_style} onClick={onCreate} />
      </div>
    </>
  );
};

export default StudentInput;