import React, { useState } from 'react';

const Kurly = () => {
  const [kNum, setKNum] = useState(1); // 
  const kPrice = 14320 //  

  const onPlusFn = () => {
    setKNum(kNum + 1);
  }
  const onMinusFn = () => {
    if (kNum === 1) return;
    setKNum(kNum - 1);
  }

  const totalPrice = kNum * kPrice; //   =  *  

  return (
    <>
      <article className='kurly-cart'>
        <div className='count'>
          <h3>[]   </h3>
          <div className='num'>
            <input type="button" value="-" className='num-minus' onClick={onMinusFn} />
            <span>{kNum}</span>
            <input type="button" value="+" className='num-plus' onClick={onPlusFn} />
          </div>
          <div className='price'>
            <span className='origin'>17,900</span>
            <span className='discount'>14,320</span>
          </div>
        </div>
        <div className='total-price'>
            : <span><strong>{totalPrice.toLocaleString('ko-KR')}</strong></span>
        </div>
        <input type="button" value=" " className='btn-cart' />
      </article>
    </>
  );
};

export default Kurly;