import React, { useState, useRef } from 'react';
import './ref.css'
import { click } from '@testing-library/user-event/dist/click';

const Useref = () => {
  // 1. pw, click, validate   state 
  const [inputs, setInputs] = useState({
    password: '', //      
    clicked: false, //             
    validated: false //      /  
  });

  // 2.  
  const { password, clicked, validated } = inputs;

  // 3. useRef 
  const passwordInput = useRef(); //   useRef ....

  // 4.        
  const onInputChange = (e) => {
    setInputs({
      password: e.target.value //     
    })
  }

  // 5. ''        
  const onInputCheack = (e) => {
    e.preventDefault();

    setInputs({
      clicked: true, //    true 
      validated: password === '1234' //     
    });

    if (password !== '1234') {
      passwordInput.current.focus(); //  
    }
  }

  // 6.       
  const onClear = (e) => {
    setInputs({
      password: ''
    })
  }

  return (
    <>
      <h3 className="title3">useRef   DOM </h3>
      <p>ref '  DOM       '    HOOKS useRef     </p>

      <p>javascript: DOM  getElementById(), querySelector()</p>
      <p>jQuery : DOM  $('')</p>
      <p> jsx DOM id   DOM     </p>
      <p>     id     .  ref   ,        </p>

      <dl>
        <dt>DOM   </dt>
        <dd> input </dd>
        <dd>  </dd>
        <dd>Canvas  </dd>
        <dd>video play/pause  </dd>
        <dd>...</dd>
      </dl>

      <h3 className='title3'>.      </h3>
      <p>       DOM  '' ,        DOM  (focus,  )  </p>

      <dl>
        <dt>ref, useRef    </dt>
        <dd>                 </dd>
        <dd>  DOM   ref, useRef      ( , UX)</dd>
      </dl>

      <form name='' method='' action="" className='formStyle'>
        <fieldset>
          <legend> </legend>
          <p>
            <input type="password" placeholder=' ' value={password} onChange={onInputChange} ref={passwordInput} className={clicked && (validated ? 'success' : 'failure')} />
          </p>
          <p>password ( ? '   ' : '   ')  </p>
          <p>
            <input type="button" value="" onClick={onInputCheack} />
            <input type="button" value=" " onClick={onClear} />
          </p>
        </fieldset>
      </form>
    </>
  );
};

export default Useref;