import React, { useState } from 'react';

const Usestate = () => {
  const [num, setNum] = useState(1);
  const price = 7300;
  const total = price * num;

  const onIncrease = () => {
    setNum(num + 1);
  }
  const onDecrease = () => {
    if (num == 1) {
      setNum(1);
    } else {
      setNum(num - 1);
    }
  }
  const onReset = () => {
    setNum(1)
  }

  const style = {
    width: '300px',
    padding: '10px',
    margin: '10px',
    border: '3px solid #f00'
  }
  const button = {
    padding: '3px 10px',
    fontSize: '20px',
    margin: '0 4px'
  }

  return (
    <>
      <h3 className="title3">React   Hooks</h3>
      <p> ()  useState() </p>
      <p>  state   .    state   ,   16.8 useState()    state    </p>
      <p>state   '  '   </p>
      <p>useState()  {`import React, {useState}`}   .</p>

      <pre>
        )
        const [,   ] = useState(); <br />
        const    = (); //  <br />
        const      = useState(); <br />
        const    =    [0]; <br />
        const     =    [1];
      </pre>

      <div style={style}>
        <p>  : {num}</p>
        <input type="button" value="-" onClick={onDecrease} style={button} />
        <input type="button" value="+" onClick={onIncrease} style={button} />
        <p> : 7,300</p>
        <p> : {total.toLocaleString('ko-kr')}</p>
        <p>   : <input type="button" value=" " onClick={onReset} style={button} /></p>
      </div>
    </>
  );
};

export default Usestate;