import React, {useState} from 'react';

function Form1(props) {
  // 
  //[, ] = useState(''); //state
  const [name, setName] = useState('');

  //   (e)    nameChange
  const nameChange=(e)=>{
    setName(e.target.value); //  .
  }

  //     nameReset
  const nameReset =()=>{
    //alert(' .');
    //input[type] name  value '' 
    setName(''); //   '' .== 
  }

  return (
    <>
      <h4 className="title4">1.   </h4>
      <form>
        <p> .</p>
        <p>  : {name}</p>
        <input type="text" value={name} name="name" onChange={nameChange}/>
        <input type="button" value="" onClick={nameReset} />

        <ul>
          <li>input   'onChange'  (p)  (name)   </li>
          <li>HTML   DOM  </li>
          <li>React     </li>
          <li>     state .</li>
        </ul>
      </form>
    </>
  );
}

export default Form1;