import React, {useState} from 'react';

function Form5(props) {
  
  // 1.   
  const [formData, setFormData] = useState({
    name:'', //
    age:null //
  });

  //2.    ,  
  const nameChange=(e)=>{
    const {name, value} = e.target; //,  
    setFormData(prev=>({
      ...prev, //   
      [name]:value
    }));
  }

  //    ()  
  const dataSubmit=(e)=>{
    e.preventDefault(); // 
    const age = formData.age;
    if(!Number(age)){ //  
      alert('    .'); // 
    }else{//  
      console.log(' : ', formData); //  
    }
  }

  return (
    <>
      <h4 className="title4">5.    </h4>
      <p>* ,    ,               .</p>
      <p>* if  state     'alert '  </p>
      <p>*     Number  .</p>

      <form onSubmit={dataSubmit}>
          : 
        <p><strong> : {formData.name}</strong></p>
        <p><strong> : {formData.age}</strong></p>

        <p>
          <label for="name">  : </label>
          <input type="text" name="name" id="name" onChange={nameChange} value={formData.name} />
        </p>

        <p>
          <label for="age">  : </label>
          <input type="text" name="age" id="age" onChange={nameChange} value={formData.age || ''} />
          {/*         */}
        </p>

        <p>
          <button type="submit"></button>
        </p>
      </form>
    </>
  );
}

export default Form5;